.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba($color: #000, $alpha: 0.5);
  display: flex;
  align-items: center;
  justify-content: center;

  &__content {
    width: 500px;
    background-color: #fff;
  }

  &__header {
    padding: 10px;
  }

  &__title {
    margin: 0;
  }

  &__body {
    padding: 10px;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
  }

  &__fotter {
    display: flex;
    flex-direction: row;
    justify-content:flex-end;
    gap: 20px;
    text-align: right;
    line-height: 26px;
    margin: 10px 20px 10px 0;
  }

  &__reject {
    width: 70px;
    height: 30px;
    text-align: center;
    background-color: #fff;
    color: #000;
  }

  &__accept {
    width: 70px;
    height: 30px;
    background-color: #000;
    color: #fff;
  }
}

.report {
  &__lable {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: right;
    line-height: 26px;
    margin: 0 10px 10px 0;
  }

  &__input {
    height: 24px;
    flex: 0 0 300px;
    margin-left: 10px;
  }

  &__format {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: right;
    line-height: 26px;
    margin: 0 10px 10px 0;
  }

  &__schedule {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-align: right;
    line-height: 26px;
    margin: 0 10px 10px 0;
  }

  &__radio {
    font-size: small;
  }
}